import { Component, Inject, OnInit, ViewEncapsulation } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { rumangoAnimations } from 'app/shared/animations/rumango-animations';

@Component({
  selector: 'app-select-product-popup',
  templateUrl: './select-product-popup.component.html',
  styleUrls: ['./select-product-popup.component.scss'],
  animations: rumangoAnimations
})
export class SelectProductPopupComponent implements OnInit {
  selectedCard:any = [];
  selectedIndex: number = null;
  widgets = [
    {
      id:1,
      title:"Customer Account",
      count:0,
      icon:"fa fa-money",
      isSelected:false
    }, {
      id:2,
      title:"Deposit Account",
      count:0,
      icon:"fa fa-credit-card",
      isSelected:false
    },{
      id:3,
      title:"Loan Account",
      count:0,
      icon:"fa fa-university",
      isSelected:false
    }
  ];
  constructor(@Inject(MAT_DIALOG_DATA) public data: any,
  public dialogRef: MatDialogRef<SelectProductPopupComponent>,
  private router:Router) { }

  ngOnInit(): void {
   
  }

  closeModal(){
    this.dialogRef.close();
    this.router.navigate(['home/application/list']);
  }


  setActive(item,idx){
   this.selectedIndex = idx;
   console.log(idx);
   let index = this.widgets.indexOf(item);
   this.widgets[index].isSelected = !item.isSelected;
   this.widgets[index].count= this.widgets[index].isSelected ? 1 : 0;
   console.log(index);
   if( this.widgets[index].isSelected){
     this.selectedCard.push(item);
   }else{
     this.selectedCard = [];
   }
   console.log(this.widgets[index]);
   console.log(this.selectedCard);
  }

}
