import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { MatDrawer } from '@angular/material/sidenav';
import { Router } from '@angular/router';
import { CASA } from 'app/shared/models/casa';
import { MainNavigationComponent } from '../../main-navigation/main-navigation.component';
import { OperationsComponent } from '../../operations/operations.component';

@Component({
  selector: 'app-casa',
  templateUrl: './casa.component.html',
  styleUrls: ['./casa.component.scss']
})
export class CasaComponent implements OnInit {
  @Input("casaData") casaData: any;
  accType: any = "All";
  accItems: any;

 
  @ViewChild('drawer', { static: true }) public drawer!: MatDrawer;
  allCasa: any;
  flag1: boolean=true;
  flag3: boolean;
  flag2: boolean;
  filteredResponseSavingAccount: any;
  filteredResponseCurrentAccount: any;

  constructor(private router: Router,private operation:OperationsComponent,private mainNavigation :MainNavigationComponent) { }

  ngOnInit() {
    console.log("CASA Screen")
    this.allCasa= this.casaData;
    console.log(this.allCasa);

    this.filteredResponseSavingAccount = this.allCasa.filter(res => res.accountType == "Savings");
    console.log("filteredResponseSave",this.filteredResponseSavingAccount)
    
    this.filteredResponseCurrentAccount = this.allCasa.filter(res => res.accountType == "current");
    console.log("filteredResponseSavingAccount",this.filteredResponseSavingAccount)

    this.accItems = this.allCasa[0].children ;
  //  this.accTypeSelected('All');

  }
 oneClick()
 {
   this.flag1=true;
   this.flag2=false;
   this.flag3=false;
 }
 twoClick()
 {
   this.flag2=true;
   this.flag1=false;
   this.flag3=false;
 }
 threeClick()
 {
   this.flag3=true;
   this.flag2=false;
   this.flag1=false;
 }
  accTypeSelected(accountType){
    console.log('Acc Type ' + accountType);
    this.accType = accountType ;
    
  }

  selectedItem(item){
    console.log('Item ' + item);
    this.accItems = item ;

  }

  goToAccDetails(accItems){
    console.log(accItems);
    this.router.navigate(['/others/mainNavigation'],{ state: accItems });
    // this.router.navigate(['/others/casaAccountDetails'],{ state: accItems });
    // this.router.navigate(['/others/casaAccountDetails']);
this.closeDrawer();
this.mainNavigation.selectMenu('Account',accItems);
  }
  closeDrawer(){
  
    this.operation.closeDrawer();
   
  }
}
