abbreviateNumber(value) {
  this.newValue = value;
  if (value >= 1000) {
      var suffixes = ["", "K", "M", "B","T", "Q" , "E" , "Z" , "Y"];
      var suffixNum = Math.floor( (""+value).length/3 );
       this.shortValue;
      for (var precision = 2; precision >= 1; precision--) {
          this.shortValue = parseFloat( (suffixNum != 0 ? (value / Math.pow(1000,suffixNum) ) : value).toPrecision(precision));
          var dotLessShortValue = (this.shortValue + '').replace(/[^a-zA-Z 0-9]+/g,'');
          if (dotLessShortValue.length <= 2) { break; }
      }
      if (this.shortValue % 1 != 0)  this.shortValue = this.shortValue.toFixed(1);
      this.newValue = this.shortValue;
      this.suffix =  suffixes[suffixNum];
  }
  console.log(this.suffix);
  
  console.log(this.newValue);
   
}
