import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { SelectProductPopupComponent } from './select-product-popup/select-product-popup.component';
// import * as bandData from './powermetal.json';

interface dropdownItems {
  value: string;
  viewValue: string;
}


@Component({
  selector: 'app-blank',
  templateUrl: './app-blank.component.html',
  styleUrls: ['./app-blank.component.scss']
})
export class AppBlankComponent implements OnInit {

  constructor( private dialog: MatDialog, private router:Router) { }
  gridColumns = 2;
  public columns: number = 2;
  showFiller = false;
  // public bands: any = (bandData as any).default;
  AccountNo = 100000410689;
  AvailableBalance = "$ 12,90,000";
  Day = [ "Today" , "Yesterday" , "11/01/2020" ];
  to = "To ";
  sent = "Sent";
  conversion = "Conversion";
  usd = " USD";
  time = ["19:37" , "18:30" , "19:37"]
  firstName = [ "Martin " , "Sam " , "EUR to USD" ];
  secondName = [ "Michael" , "Jane" ]
  Amount = [ -1232.56 , -132.56, +213.00];
  statuses: dropdownItems [] = [
    {value: 'Status-0', viewValue: 'Status'},
    {value: 'Status-1', viewValue: 'Status1'},
    {value: 'Status-2', viewValue: 'Status2'} 
  ];
  types: dropdownItems [] = [
    {value: 'type-0', viewValue: 'Type'},
    {value: 'type-1', viewValue: 'Type1'},
    {value: 'type-2', viewValue: 'Type2'}
  ];



  ngOnInit() {
  }
  public getStyles() {
    return {
      display: 'grid',
      'grid-template-columns': `repeat(${this.columns}, 1fr)`,
      'justify-items': 'center',
    };
  }

  openPopUp() {
    this.router.navigate(['/home/application/list'], { queryParams: { account: 'new' } });

    // let dialogRef: MatDialogRef<any> = this.dialog.open(SelectProductPopupComponent, {
    //   width: '720px',
    //   disableClose: true,
    //   data: { title: "title", payload: [] }
    // })
    // dialogRef.afterClosed()
    //   .subscribe(res => {
    //     if(!res) {
    //       // If user press cancel
    //       return;
    //     }
    //   })
  }

}
