      console.log(data);
      this.documentForm = this.fb.group({
        documentsList: this.fb.array([this.createItem()]),
        doc_list:
          data === 'DATA FETCH'
            ? this.fb.array([])
            : this.fb.array([this.createDocItem()]),
        id: [''],
        upload: ['', [Validators.required]],
      });
      console.log(this.documentForm.get('documentName'));
      console.log(this.documentForm.value.documentsList[0].documentName);
      console.log('this.documentForm', this.documentForm);
      console.log('documentsList', this.documentsList);
      console.log('doc_list', this.doc_list);
      if (data === 'DATA FETCH') {
        this.dataFetched = true;
        this.dataFetchedValue = data;
      } else {
        this.dataFetched = false;
      }
