  function2(webcamImage, i) {
    console.log(i);
    const cId: string = this.customerId;
    const uploadData2 = new FormData();
    const data = {
      customerId: this.refreshCustomerId,
      documentName: this.documentForm.value.documentName,
      documentType: '3',
      fileType: webcamImage._mimeType,
      fileName: 'doc',
      verificationType: 'KYC verification',
      documentSide: 3,
      idNumber: this.panNumber,
    };
    console.log(webcamImage.imageAsDataUrl);
    this.otherImageArray[i] = webcamImage.imageAsDataUrl;
    // this.doOCR(this.imageURL2);
    this.displayVal2 = false;
    let file = this.dataURLtoFile(webcamImage.imageAsDataUrl, 'Aadhar');

    uploadData2.append('data', JSON.stringify(data));

    uploadData2.append('file', file);
    this.tokenService.upload(uploadData2).subscribe(
      (res: any) => {
        this.progressValOthersArray[i] = 100;
        if (res.type === 4) {
          if (res.body) {
            console.log('res.body.id', res.body.id);
            this.assignIdValues('others', res.body.id);
          }
        }
        this.documentForm.get('backSide').patchValue(true);
      },
      (error) => {
        if (error.status == 201) {
          this.ff = true;
          this.documentForm.get('backSide').patchValue(true);
          this.shotToast('Document Uploaded!');
        }
      }
    );
  }

