import { Component, OnInit, ViewChild } from '@angular/core';
import { MediaChange, MediaObserver } from '@angular/flex-layout';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatSidenav } from '@angular/material/sidenav';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { Observable, of, Subscription } from 'rxjs';

@Component({
  selector: 'app-others',
  templateUrl: './others.component.html',
  styleUrls: ['./others.component.scss']
})
export class OthersComponent implements OnInit {

  isMobile;
  public viewMode: string = 'grid-view';
  screenSizeWatcher: Subscription;
  isSidenavOpen: Boolean = true;
  @ViewChild(MatSidenav) private sideNav: MatSidenav;
  public categories:any = [];

  constructor(private mediaObserver: MediaObserver, private router:Router, private dialog: MatDialog,
    private snack: MatSnackBar,private loader: AppLoaderService) { }

  ngOnInit() {
    this.getCategories();
  }

  updateSidenav() {
    let self = this;
    setTimeout(() => {
      self.isSidenavOpen = !self.isMobile;
      self.sideNav.mode = self.isMobile ? 'over' : 'side';
    })
  }

  sideNavInit() {
    this.isMobile = this.mediaObserver.isActive('xs') || this.mediaObserver.isActive('sm');
    this.updateSidenav();
    this.screenSizeWatcher = this.mediaObserver.media$.subscribe((change: MediaChange) => {
      this.isMobile = (change.mqAlias == 'xs') || (change.mqAlias == 'sm');
      this.updateSidenav();
    });
  }

  toggleSideNav() {
    this.sideNav.opened = !this.sideNav.opened;
  }

  goToHomeRoute(){
    this.router.navigate(['/others']);
  }

  public getCategories(){
    this.categories = [
      {
        id:1,
        title:'Overview',
        state:'application/list',
        icon:'account_balance_wallet',
        isOpen:"false",
        
      },{
        id:2,
        title:'Accounts',
        state:'create-customer-account',
        icon:'credit_card',
        isOpen:"false"
      },{
        id:3,
        title:'Cards',
        state:'create-customer-account',
        icon:'style',
        isOpen:"false"
      },{
        id:4,
        title:'Investments',
        state:'create-customer-account',
        icon:'assessment',
        isOpen:"false"
      },{
        id:5,
        title:'Loans',
        state:'create-customer-account',
        icon:'card_membership',
        isOpen:"false"
      },{
        id:6,
        title:'Reports',
        state:'create-customer-account',
        icon:'done_all',
        isOpen:"false"
      },
      {
        id:6,
        title:'Settings',
        state:'create-customer-account',
        icon:'settings',
        isOpen:"false"
      },
    ];
  }
}
