import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-operations',
  templateUrl: './operations.component.html',
  styleUrls: ['./operations.component.scss']
})
export class OperationsComponent implements OnInit {

  @Input() drawer;
  @Input("operationData") operationData: any;
  constructor() { }
  dashboardcalenderData: Object;
  dashboardCalenderEnable: boolean = false;
  accountInformationData: Object;
  accountInformationEnable: boolean = false;
  casaData: Object;
  casaEnable: boolean = false;
  ngOnChanges() {
    this.operationType();
  }
  

  ngOnInit() {
    console.log(this.operationData);
    this.operationType();
  }
  operationType() {
    console.log(this.operationData[0].type);
  
    this.dashboardCalenderEnable = false;
    this.accountInformationEnable = false;
    this.casaEnable = false;

    if (this.operationData[0].type == "dashboardcalender") {
      console.log(" if operation screen")
      this.dashboardCalenderEnable = true;
      this.dashboardcalenderData = this.operationData;
    } 
     if (this.operationData[0].type == "Account Information") {
      console.log(" if Account Information screen")
      this.accountInformationEnable = true;
      this.accountInformationData = this.operationData;
    } 
     if (this.operationData[0].type == "CASA") {
      console.log(" CASA")
      this.casaEnable = true;
      this.casaData = this.operationData;
    }
  }

  closeDrawer() {
    this.drawer.close();
  }
}
