  onChange(event, i) {
    console.log("toggle event", event.checked, "index", i);

    if (event.checked == true) {
      this.toggle = true;
      this.index = i;
    }
    if (event.checked == false && this.index === i) {
      this.toggle = false;
    }
    console.log("this.toggle", this.toggle)
  }

  isAllSameDigit(event) {
    let count = 0, number = event.target.value;
    if (number != '') {
      for (var i = 0; i < number.length; i++) {
        if (0 == number[i]) {
          count++;
        }
      }
      if (count == number.length) {
        console.log('if loop')
        this.isAllSameDigitVal = true;
      }
      else {
        this.isAllSameDigitVal = false;
      }
    }else{
      this.isAllSameDigitVal = false;
    }
  }

