import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { MatDrawer } from '@angular/material/sidenav';
import { DomSanitizer } from '@angular/platform-browser';
import { AccountInformation } from 'app/shared/models/accountinformation';
import { ApiService } from 'app/shared/services/api.service';
import { OperationsComponent } from '../../operations/operations.component';
import { MainNavigationComponent } from '../../main-navigation/main-navigation.component';

@Component({
  selector: 'app-account-information',
  templateUrl: './account-information.component.html',
  styleUrls: ['./account-information.component.scss']
})
export class AccountInformationComponent implements OnInit {
  @Input("accountInformationData") accountInformationData: any;
  accountInformation:AccountInformation;
  @ViewChild('drawer', { static: true }) public drawer!: MatDrawer;
  custId:any;
  profileData: any;
  image: Object;
  imageUrl: any = null;
  imageURL: any;
  images: any[]=[];
  constructor(private operation:OperationsComponent,private apiService:ApiService,private sanitizer: DomSanitizer, 
    mainNavigation :MainNavigationComponent) { }

  ngOnInit() {
    this.accountInformation= this.accountInformationData;
    // this.custId=localStorage.getItem('custId');
    // console.log(this.custId)
    // this.custId=localStorage.getItem('customerId');
    let number = localStorage.getItem('customerId');
    this.custId = number.match(/(\d+)/)
    console.log(this.custId[0]);
    this.getProfile();
  }
getProfile(){
  this.apiService.getProfileDetails(this.custId[0])
  .subscribe((data:any) => {
    this.profileData = data;
    console.log(this.profileData);

    this.imageUrl = data.profileImage;
    console.log(this.profileData,"Profile Data");
  },(error:any) => {
    console.log(error);
  });
  this.getSignature(this.custId[0])
}
  getSignature(custId: any) {
    this.apiService.fetchSignatureInfo(custId).subscribe(resp=>{
      console.log("resp for signature ",resp)
      this.images=resp;
      resp.forEach((element,index)=>{
        this.imageURL = resp[index].fileUrl;
        console.log("File URL", this.imageURL)
      })
    })
  }
  closeDrawer(){
  
    this.operation.closeDrawer();
   
  }
}
