import { Component, OnInit } from '@angular/core';
import { ChangeDetectorRef, Inject, ViewChild } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SignaturePad } from 'angular2-signaturepad';
import { ApiService } from 'app/shared/services/api.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from 'app/views/home/account.service';

@Component({
  selector: 'app-customer-onboarding-mandate-esign',
  templateUrl: './customer-onboarding-mandate-esign.component.html',
  styleUrls: ['./customer-onboarding-mandate-esign.component.scss']
})
export class CustomerOnboardingMandateEsignComponent implements OnInit {
  signatureImg: any;
  @ViewChild(SignaturePad) signaturePad: SignaturePad;
  isDone: boolean = false;
  accountId:any;
  signaturePadOptions: Object = {
    'minWidth': 2,
    'canvasWidth': 700,
    'canvasHeight': 300
  };
  diasableDone:boolean =true;
  // data:image/png;base64
  accountdetailsForStatusUpdate: Object;
  count: any=0;
  show: boolean;
  res: Object = {'enable':false, 'image':null};

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<CustomerOnboardingMandateEsignComponent>,
    private ls: LocalStoreService,
    private accountService: AccountService,
    private apiService: ApiService,
    private appEntryService: ApplicationEntryStageService,
    private cdr:ChangeDetectorRef
  ) { }

  ngOnInit(): void {
    this.accountId = this.ls.getItem('accountId');
    console.log("popup response data:",this.data);
    this.isDone = this.data.isSignLater;
    this.cdr.markForCheck();
  }
  ngAfterViewInit() {
    // this.signaturePad is now available
    this.signaturePad.set('minWidth', 2);
    this.signaturePad.clear();
  }

  drawComplete() {
    console.log(this.signaturePad.toDataURL());
    const value=this.signaturePad.toDataURL();
    console.log('data :: ',value.toString().includes('data:image/png;base64'));
    if(value.toString().includes('data:image/png;base64')){
      this.diasableDone=false;
    }else{
      this.diasableDone=true;
    }
  }

  drawStart() {
    console.log('begin drawing');
  }

  clearSignature() {
    this.signaturePad.clear();
  }

  savePad() {
    const base64Data = this.signaturePad.toDataURL();
    this.signatureImg = base64Data;
  }



  close1()
  {
    if(this.data.screen === "NewCustomerOnboarding") {
      this.dialogRef.close(this.res);
    }
    else {
      this.dialogRef.close(true);
      this.ls.setItem('enable',false);
    }
  }

  close() {
    if(this.data.screen === "NewCustomerOnboarding") {
      this.dialogRef.close(this.res);
    }
    else {
      this.dialogRef.close(true);
      this.ls.setItem('enable',true);
    }
  
  }

  onSubmit() {
    this.signatureImg = this.signaturePad.toDataURL();
    console.log("this.signatureImg::",this.signatureImg);    
    let data: any = {};
    console.log("this.data::",this.data)
   
    this.res = {enable:true, image:this.signaturePad.toDataURL()};

    data.accountSignatureId = this.data.accountSignatureId;
    console.log( data.accountSignatureId);
    
    data.customerId = this.data.customerId;
    data.documentName = this.data.customerId + "Signature";
    data.documentType = "10"
    data.fileType = "image/jpeg";
    data.fileName = this.data.customerId + "signature"+".jpg";
    data.verificationType= "New Customer Signature";
    data.signatureType= this.data.signatureType;
    const dataURL = this.signaturePad.toDataURL('image/png');
    const datam = atob(dataURL.substring('data:image/png;base64,'.length)),
      asArray = new Uint8Array(datam.length);
    for (var i = 0, len = datam.length; i < len; ++i) {
      asArray[i] = datam.charCodeAt(i);
    }
    const blob = new Blob([asArray], { type: 'image/png' });
    const txtCont = '{"noOfSignatures":' +  this.data.signCount +', "uploadModel":' + JSON.stringify(data) + ' } ';

    const uploadData = new FormData();
    uploadData.append('data', txtCont);
    uploadData.append('file', blob);

    console.log("uploadData::",uploadData)
    this.apiService.uploadSignature(uploadData).subscribe((op) => {
      console.log("op ::");
      this.apiService.setSignData(op);
      if(op)
      {
      this.show=true;
      // this.res = op;
      console.log("this.res ::",this.res);
      }
    });
    if(this.data.screen !== "NewCustomerOnboarding") {
      this.isDone = true;
    }
    else {
      this.close();
    }
    console.log(this.isDone);
    this.ls.setItem('eSignStage', true);
  }

 

 



}
