  public addGenericDocSide(i, j, documentSide) {
    const control = <FormArray>this.documentForm.get('documentArr')['controls'][i]
      .get('documentSides')['controls'][j].get(`${documentSide}`);
    if (documentSide === 'frontSideArr') {
      control.push(this.createFrontArraySide());
    } else if (documentSide === 'backSideArr') {
      control.push(this.createBackArraySide());
    } else if (documentSide === 'otherSideArr') {
      control.push(this.createOtherArraySide());
    } else if (documentSide === 'pages') {
      control.push(this.createPagesArray());
    }
    this.ocrService.setenableNext(true);
    this.disableNext=true;
  }

