  public finalUploadMethod(uploadData, i, j, k, docVal, documentName) {
    
    this.loader.open();
    this.uploadSubscribe = this.apiService.upload(uploadData).subscribe(
      (res: any) => {
        console.log('Response -- ', res);
        if (res.body) {
          this.updatedDataAfterUpload(res, i, j, k, docVal, res.body);
          this.shotToast('Document Uploaded!');
        }
      }, 
      (error: any) => {
        console.log('File upload ::', error);
        if (error.status == 201) {
          this.shotToast('Document Uploaded!');
          this.loader.close();
        } else {
          this.shotToast(error.message);
        }
      }
    );
    this.ocrService.setenableNext(false);
    this.disableNext=false;
    console.log( this.disableNext)
    this.loader.close();
  }

  public formatBytes(bytes, decimals) {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const dm = decimals < 0 ? 0 : decimals;
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
  }

