  public getCustomerDocuments(cId) {
    console.log("==================> ",this.customerId);
    console.log(this.screenInfo);
    if(this.screenInfo.screenName=='Cad Document Upload'){
      this.iscardDoc=true;
    }else{
      this.iscardDoc=false;
    }
    if(this.screenInfo.isEdit == true){
      this.flex = "35%"
      this.flex1 = "35%"
    }
    else{
      this.flex = '28%'
      this.flex1 = '25%'
    }
    console.log("cid", cId);
    var custId= this.customerId ? this.customerId: this.screenInfo?.customerId;
    var isDataPresentCount=0;
    for (const key in this.document_type) {
      if (Object.prototype.hasOwnProperty.call(this.document_type, key)) {
        const element = this.document_type[key];
        console.log(element, key, cId);
        this.apiService.getCustomerDocuments(custId, element)
          .subscribe((data) => {
            this.loader.close();
            console.log("----------------- ",key, data);
            if(data != null && data != undefined && data?.length !=0 ){
              var sort = _.sortBy(data, ["documentSide"]);
              this.addDocument(data[0]);
              this.getCustomerDocumentsFormBuild(key, sort);
              this.documentsLength=this.documentsLength+data.length;
              isDataPresentCount=0;
              this.ocrService.setenableNext(false);
              this.disableNext=false;
              for(let i=0;i<=data.length;i++)
              {
                this.selectedOptions2.push(data[i].documentName);
              }
            }else{
              this.documentsLength=0;
              isDataPresentCount++;
              if(isDataPresentCount == this.document_type.length){
                this.buildForm(null)
              }
            }
           
          });
      }
     

    }
  }

  public getCustomerDocumentsFormBuild(i, data) {
    let k = 0;
    for (const key in data) {
      if (Object.prototype.hasOwnProperty.call(data, key)) {
        const element = data[key];
        console.log(key, '--element-- ', element);
        if (element.documentSide == 1) {
          this.updatedDataAfterUpload('', i, 0, 0, 'frontSideArr', element);
        } else if (element.documentSide == 2) {
          this.addGenericDocSide(i, 0, 'backSideArr')
          this.updatedDataAfterUpload('', i, 0, 0, 'backSideArr', element);
        } else if (element.documentSide == 3) {
          this.addGenericDocSide(i, 0, 'otherSideArr')
          this.updatedDataAfterUpload('', i, 0, 0, 'otherSideArr', element);
        } else if (element.documentSide >= 4) {
          this.addGenericDocSide(i, 0, 'pages')
          this.updatedDataAfterUpload('', i, 0, k, 'pages', element);
          k++;
        }
      }
    }
  }

