  public triggerSnapshot(event, docVal, docSideEnum, i, j, k, property, documentValFromUI) {
    const dialogRef = this.dialog.open(WebcamCaptureComponent, {});
    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: `, result);
      this.uploadDoc(event, '', docVal, docSideEnum, i, j, k, documentValFromUI, result.data, result)
      const control = this.getDocSideFormArrayControl(i, j, k, docVal);
      control.at(k).get(`${property}`).setValue(false);
      if (result.response === 'captured') {
        control.at(k).get('fileUrl').setValue(result.data);

      } else {
      }

    });
  }

