import { Component, OnDestroy, OnInit } from "@angular/core";
import { MatDialogRef } from "@angular/material/dialog";
import { Subject } from "rxjs";
import { WebcamImage, WebcamInitError } from "ngx-webcam";

@Component({
  selector: 'app-webcam-capture',
  templateUrl: './webcam-capture.component.html',
  styleUrls: ['./webcam-capture.component.scss']
})
export class WebcamCaptureComponent implements OnInit {
  readonly imageTrigger: Subject<void> = new Subject<void>();
  error?: string;

  constructor(private readonly dialogRef: MatDialogRef<WebcamCaptureComponent>) {}

  ngOnInit(): void {
  }
  captureImage(webcamImage: WebcamImage): void {
    this.dialogRef.close({
        data:webcamImage,
        response:'captured'
      });
  }

  triggerSnapshot(): void {
    this.imageTrigger.next();
  }

  handleInitError(error: WebcamInitError): void {
    console.warn(error);
    this.error = JSON.stringify(error);
  }

  ngOnDestroy(): void {
    this.imageTrigger.complete();
    console.log("ngOnDestroy completed");
  }
  close(){
    this.dialogRef.close({
      data:'',
      response:'cancel'
    });
  }
}
