import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-recent-transactions',
  templateUrl: './recent-transactions.component.html',
  styleUrls: ['./recent-transactions.component.scss']
})
export class RecentTransactionsComponent implements OnInit {
  customerId:any;
  accountId:any;
  recentActivities: any;
  constructor(private localstorage: LocalStoreService,private apiService:ApiService,
    private router: Router,public dialogRef: MatDialogRef<RecentTransactionsComponent>) { }

  ngOnInit(): void {
    this.customerId = this.localstorage.getItem('customerId');
    console.log("customerId:",this.customerId);
    this.recentTransactionList();
  }
  close(){
    this.dialogRef.close();
    this.router.navigateByUrl('/others/mainNavigation');
  }
  recentTransactionList(){
    this.apiService.getDashBoardRecentTransactions(this.customerId).subscribe((data :any)=>{
      console.log("data-->",data)
      // for(let i=0;i<data.length;i++){
        this.recentActivities=data;
        console.log(this.recentActivities)
      // }
    })
  }

}
